import React from "react";

class InputCompForm extends React.Component{
  constructor(props){
    super();
    this.state = {
      text: ""
    };
    this.inputValue = this.inputValue.bind(this)
    this.handleClick = this.handleClick.bind(this)
  }

  inputValue(e){
    this.setState({text: e.target.value});
  }

  handleClick(){
    const a = [this.state.text.slice(44).split(",").map(x => x.substring(0, 1).toUpperCase()+x.substring(1))];
    const newrisuto = this.props.risuto.concat(a);
    this.props.updateState({risuto: newrisuto});
  }

  render(){
    return(
			<div>
        <input type="text" onChange={this.inputValue}/>
        <input type="button" value="追加" onClick={this.handleClick} />
      </div>
    )
  }
}

export default InputCompForm;