import { Link } from "gatsby";
import React from "react";
import InputCompForm from "./input-comp-form";
import TeamCompositionTable from "./team-composition";

class TFTCompMemoTool extends React.Component{
  constructor(props){
    super();
    this.state = {
      risuto: [
        ["Yasuo", "Yone"]
      ]
    }
  }

  updateState(state){
    this.setState(state);
  }

  render(){
    return(
			<div>
        <h1>TFTの構成がメモ出来るやつ</h1>
        <p><Link to="https://lolchess.gg/simulator/4.0">TFT シナジーシミュレータ</Link>のShareボタンから取得できるURLを貼ると構成が表示されます。</p>
        <p>まだまだ開発中</p>
        <InputCompForm risuto={this.state.risuto} updateState={this.updateState.bind(this)}/>
      	<TeamCompositionTable risuto={this.state.risuto} />
			</div>
    )
  }
}

export default TFTCompMemoTool;